"use client";
import { useEffect, useState, useRef } from "react";
import { fetchMessages } from "@/services/api";
import { initSocket, disconnectSocket } from "../services/socket";

function LoadingDots() {
  return <span className="animate-pulse text-sm text-gray-500">Typing...</span>;
}

export default function ChatBox() {
  const [messages, setMessages] = useState<any[]>([]);
  const [input, setInput] = useState("");
  const [aiTyping, setAiTyping] = useState(false);
  const socketRef = useRef<any>(null);

  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;

  useEffect(() => {
    if (!token) return;

    // Initialize socket
    const socket = initSocket(token);
    socketRef.current = socket;

    // Fetch existing messages
    fetchMessages(token).then(setMessages);

    // Listen for new messages
    socket.on("newMessage", (msg) => {
      setMessages((prev) => [...prev, msg]);
    });

    // Listen for AI typing indicator
    socket.on("aiTyping", (typing: boolean) => {
      setAiTyping(typing);
    });

    // Handle connection errors
    // socket.on("connect_error", (err: any) => {
    //   console.error("Socket connection error:", err);
    // });


    return () => {
      disconnectSocket();
    };
  }, [token]);

  const handleSend = () => {
    if (!input.trim() || !socketRef.current) return;
    socketRef.current.emit("sendMessage", input.trim());
    setInput("");
  };

  return (
    <div className="p-4 h-screen flex flex-col">
      {/* Messages Section */}
      <div className="flex-1 overflow-auto border mb-2 p-2 space-y-2">
        {messages.map((msg, idx) => (
          <div
            key={idx}
            className={`p-2 rounded-md max-w-sm ${msg.sender && msg.sender.username
              ? "bg-blue-100 text-right ml-auto"
              : "bg-gray-100 text-left"
              }`}
          >
            {msg.content}
          </div>
        ))}
        {aiTyping && (
          <div className="p-2 bg-gray-200 rounded-md max-w-sm text-left">
            <LoadingDots />
          </div>
        )}
      </div>

      {/* Input Section */}
      <div className="flex">
        <input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          className="flex-1 border p-2"
          placeholder="Type your message..."
        />
        <button
          onClick={handleSend}
          className="ml-2 bg-blue-600 text-white px-4 py-2"
        >
          Send
        </button>
      </div>
    </div>
  );
}
